body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  background-color: #141414; }

* {
  font-family: "Encode Sans Condensed", sans-serif;
  margin: 0;
  color: white;
  letter-spacing: 2px; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a {
  text-decoration: none !important; }

.Font-quicksand {
  font-family: "Quicksand", sans-serif; }

.Blank-xxsmall {
  padding-top: 1vh; }

.Blank-xsmall {
  padding-top: 5vh; }

.Blank-small {
  padding-top: 10vh; }

.Blank-medium {
  padding-top: 20vh; }

.Blank-large {
  padding-top: 30vh; }

.Blank-xlarge {
  padding-top: 50vh; }

.Blank-xxlarge {
  padding-top: 70vh; }

.Blank-xxxlarge {
  padding-top: 120vh; }

.Text-xsmall {
  font-size: calc(0.7rem + 0.5vw); }

.Text-small {
  font-size: calc(1rem + 0.6vw); }

.Text-medium {
  font-size: calc(1.5rem + 1vw); }

.Text-large, .Module-mainlogo {
  font-size: calc(2rem + 1.5vw); }

.Text-xlarge {
  font-size: calc(3.5rem + 2vw); }

.Text-xxlarge {
  font-size: calc(6.5rem + 4vw); }

.Text-bold, .Module-mainlogo {
  font-weight: 900; }

.Text-light {
  font-weight: 400; }

.Text-xlight {
  font-weight: 100; }

.Text-wide {
  letter-spacing: 10px; }

.Text-xwide {
  letter-spacing: 50px; }

.Style-flex {
  display: flex;
  flex-wrap: wrap; }
  .Style-flex-center {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    word-break: break-all; }

.Style-center {
  text-align: center;
  align-items: center; }

.Align-vertical-middle {
  vertical-align: middle; }

.Background-header {
  background-color: #2c4ac4;
  padding-bottom: 30px; }

.Background-header-index {
  background-color: #2c4ac4;
  padding-bottom: 10px; }

.Background-background {
  background-color: #141414; }

.Animation-PageUpOpacity {
  -webkit-animation: PageUpOpacity-Animate 1s ease-in-out;
          animation: PageUpOpacity-Animate 1s ease-in-out; }

@-webkit-keyframes PageUpOpacity-Animate {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes PageUpOpacity-Animate {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.Animation-PageUpSpacing img {
  -webkit-animation: PageUpSpacing-Animate 1s ease;
          animation: PageUpSpacing-Animate 1s ease; }

@-webkit-keyframes PageUpSpacing-Animate {
  0% {
    margin-left: 3rem;
    margin-right: 3rem; }
  100% {
    margin-left: 1rem;
    margin-right: 1rem; } }

@keyframes PageUpSpacing-Animate {
  0% {
    margin-left: 3rem;
    margin-right: 3rem; }
  100% {
    margin-left: 1rem;
    margin-right: 1rem; } }

.Module-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0; }

.Module-mainlogo {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 900;
  color: white;
  padding-left: 20px;
  padding-top: 3px;
  float: left;
  -webkit-transition: color 0.1s ease-in-out;
  transition: color 0.1s ease-in-out; }

.Module-mainlogo:hover {
  color: #9ebcff; }

.Module-mainlogo:active {
  margin-top: 0;
  color: #2c4ac4; }

.Module-loginbutton {
  float: right;
  font-size: 1.1rem;
  font-weight: 500;
  margin-top: 13px;
  margin-right: calc(5px + 1vw);
  color: white;
  -webkit-transition: color 0.1s ease-in-out;
  transition: color 0.1s ease-in-out; }

.Module-loginbutton:hover {
  color: #9ebcff; }

.Module-loginbutton:active {
  color: #2c4ac4; }

.Module-header-index {
  margin-left: 1.2rem;
  color: black;
  text-decoration: none; }

.Module-notfound {
  font-weight: 900;
  -webkit-animation: notfound-animate 5s infinite;
          animation: notfound-animate 5s infinite; }

@-webkit-keyframes notfound-animate {
  0% {
    color: white;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    font-size: 120px; }
  50% {
    color: #2c4ac4;
    font-size: calc(120px + 11vw);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  100% {
    font-size: 120px;
    color: white;
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg); } }

@keyframes notfound-animate {
  0% {
    color: white;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    font-size: 120px; }
  50% {
    color: #2c4ac4;
    font-size: calc(120px + 11vw);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  100% {
    font-size: 120px;
    color: white;
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg); } }

.Module-notfoundmove {
  margin-top: 2rem;
  -webkit-animation: notfoundmove-animate 5s infinite;
          animation: notfoundmove-animate 5s infinite; }

@-webkit-keyframes notfoundmove-animate {
  0% {
    margin-top: 2rem; }
  50% {
    margin-top: 8rem; }
  100% {
    margin-top: 2rem; } }

@keyframes notfoundmove-animate {
  0% {
    margin-top: 2rem; }
  50% {
    margin-top: 8rem; }
  100% {
    margin-top: 2rem; } }

.Module-input {
  margin-left: calc(50vw - 180px);
  margin-right: calc(50vw - 180px); }

.Module-errormessage {
  margin-top: 100px;
  margin-bottom: 100px;
  color: red;
  -webkit-animation: errormessage-animate 0.5s ease-in-out;
          animation: errormessage-animate 0.5s ease-in-out; }

@-webkit-keyframes errormessage-animate {
  0% {
    color: color(24, 24, 24); }
  100% {
    color: red; } }

@keyframes errormessage-animate {
  0% {
    color: color(24, 24, 24); }
  100% {
    color: red; } }

